import React, { Component } from 'react';
import {connect} from 'react-redux'
import {updateObject,checkValidation} from '../../../shared/Utility'
import classes from './ContactData.css'
import Button from '../../../components/UI/Button/Button'
import axios from '../../../axios-order'
import Spinner from '../../../components/UI/Spinner/Spinner'
import Input from '../../../components/UI/Input/Input'
import WithErrorHandler from '../../../hoc/withErrorHandler/withErrorHandler'
import * as action from '../../../store/action/index'
class ContactData extends Component {
    state = {
        orderForm : {
            name: {
                elementType : 'input',
                elementConfig: {
                    text: 'input',
                    placeholder:'Your Name'
                },
                value : '',
                validation: {
                    required:true
                },
                touched: false
            },
            street:{
                elementType : 'input',
                elementConfig: {
                    text: 'input',
                    placeholder:'Street'
                },
                value : '',
                validation: {
                    required:true
                },
                valid:false,
                touched: false
            },
            zipcode:{
                elementType : 'input',
                elementConfig: {
                    text: 'input',
                    placeholder:'ZIP CODE'
                },
                value : '',
                validation: {
                    required:true,
                    minLength:5,
                    maxLength:5
                },
                valid:false,
                touched: false
            },
            country:{
                elementType : 'input',
                elementConfig: {
                    text: 'input',
                    placeholder:'Country'
                },
                value : '',
                validation: {
                    required:true
                },
                valid:false,
                touched: false
            },
            email:{
                elementType : 'input',
                elementConfig: {
                    text: 'email',
                    placeholder:'Your Mail'
                },
                value : '',
                validation: {
                    required:true
                },
                valid:false,
                touched: false
            },
            delivery:{
                elementType : 'select',
                elementConfig: {
                 options: [{ value:'fastest', displayValue:'Fastest'},
                        { value:'cheapest', displayValue:'Cheapest'}]
                },
                value : 'fastest',
                validation:{},
                valid :true
            },

        },
        formIsValid : false,
    }

    orderedHandler = (event) => {
        event.preventDefault()
        let formData = {};
        for(let formElementIdetifier in this.state.orderForm){
            formData[formElementIdetifier] = this.state.orderForm[formElementIdetifier].value
        }
        const order = {
            ingredients:this.props.ings,
            totalPrice:this.props.price,
            orderData:formData,
            userId : this.props.userId
        }
       this.props.onOrderBurger(order, this.props.token)
    }


    inputChangeHandler = (event, fromElementIdentifier) => {

       const updateFormIdentifier = updateObject(this.state.orderForm[fromElementIdentifier],{
            value : event.target.value,
            valid : checkValidation( event.target.value,this.state.orderForm[fromElementIdentifier].validation),
            touched : true
       })

       const updateOrderForm = updateObject(this.state.orderForm,{
        [fromElementIdentifier] : updateFormIdentifier
       })
    

       let formIsValid = true
       for(let inputIdentifier in updateOrderForm){
        formIsValid = updateOrderForm[inputIdentifier].valid && formIsValid
       }

       this.setState({orderForm : updateOrderForm, formIsValid:formIsValid})
    }

    render(){
        let formElementsArray = [];
        for(let key in this.state.orderForm){
            formElementsArray.push({
                id:key,
                config:this.state.orderForm[key]
            })
        }
        let form = (
            <form onSubmit={this.orderedHandler}>
                {formElementsArray.map(formElement => {
                    return <Input 
                        key={formElement.id}
                        elementType={formElement.config.elementType} 
                        elementConfig={formElement.config.elementConfig} 
                        value={formElement.config.value} 
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={(event)=>this.inputChangeHandler(event,formElement.id)} />
                })}              
                <Button disabled={!this.state.formIsValid} buttonType="Success">ORDER</Button>
            </form>
        )
        if(this.props.loading){
            form = <Spinner />
        }

        return(
            <div className={classes.ContactData}> 
                <h4>Enter your details</h4>
                {form}
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        ings : state.burgerBuilder.ingredients,
        price: state.burgerBuilder.totalPrice,
        loading: state.order.loading,
        token: state.auth.tokenId,
        userId : state.auth.userId
    };
};

const mapDispatchToProps = dispatch => {
    return{
         onOrderBurger : (orderData, token) => dispatch(action.purchaseBurger(orderData, token))
    }; 
};
export default connect(mapStateToProps, mapDispatchToProps)(WithErrorHandler(ContactData, axios))


